import RestaurantCard, { withPromotedLabel } from "./RestaurantCard";

import { useState, useEffect, useContext } from "react";

import Shimmer from "./Shimmer";
import { Link } from "react-router-dom";
import useOnlineStatus from "../utils/useOnlineStatus";
import useOnlineStatus from "../utils/useOnlineStatus";
import RestaurantCard from "./RestaurantCard";
import UserContext from "../utils/UserContext";
import useAppMenu from "../utils/useAppMenu";
import { Search } from "lucide-react";
import Filters from "./Filters";

const Body = () => {
 


  const [searchButton, setSearchButton] = useState("");
  const { LoggedInUser, setUserName } = useContext(UserContext);

  const RestaurantPromoted = withPromotedLabel(RestaurantCard);
 
  const {listOfResto,filteredResto,setFilteredResto}=useAppMenu();
 const [filterOpen,setFilterOpen]=useState(false)
 
  
  const onlineStatus = useOnlineStatus();
  if (onlineStatus === false)
    return (
      <h1>Looks like you are offline!!! Check the internet connectivity</h1>
    );

  if (listOfResto.length === 0) {
    return <Shimmer />;
  }
   const handleClick=()=>{
    const filteredSearch = listOfResto.filter((res) => {
      return res?.card?.card?.info?.name
        ?.toLowerCase()
        .includes(searchButton.toLowerCase());
    });
    filteredSearch.length > 0
      ? setFilteredResto(filteredSearch)
      : alert("Item not found");
   }
     const handleFilter=()=>{
      setFilterOpen(!filterOpen)
     }
  return (
    <div className="relative m-3">
     
      <div className=" relative flex ">
      <div className=" left-4 w-6/12 p-6 text-gray-500 ">
        <Search className="absolute m-5 w-4 text-gray-500"/>
      <input
            type="text"
            className=" pl-12 pr-10 py-5 w-full text-gray-800 bg-gray-100 border border-gray-300 rounded-full shadow-md focus:ring-2 focus:ring-black-500 focus:bg-white outline-none transition-all duration-300"
            placeholder="Search restaurant" value={searchButton}
            onChange={(e) => setSearchButton(e.target.value)}
          onKeyDown={(e) => e.key === "Enter" && handleClick()}>
          
          </input>
          </div>

          <button
            className="absolute right-187 px-4 py-1 my-9 border  border-s-black cursor-pointer rounded-lg bg-black text-white"
            onClick={handleClick}
          >
            Search
          </button>
          
      </div>
      <div className="w-3/12 ml-8 bg-gray-200 p-2 my-3 shadow-lg cursor-pointer"onClick={handleFilter}>
      <div className="flex justify-between">
        <span className="m-1 px-5 text-gray-500">Filters</span>
        <span>  </span>
      </div>
      
        {filterOpen && <Filters listOfResto={listOfResto} setFilteredResto={setFilteredResto} />}
     
      </div>
      <div className="flex flex-wrap mx-10 p-6 border border-s-black">
        {filteredResto.map((resto) => (
          <Link
            key={resto.card.card.info.id}
            to={"/restaurant/" + resto.card.card.info.id}
          >
            {resto.card.card.info.promoted ? (
              <RestaurantPromoted resData={resto} />
            ) : (
              <RestaurantCard resData={resto} />
            )}
          </Link>
        ))}
      </div>
    </div>
  );
};

export default Body;
