import { addItems } from "../utils/cartSlice";
import { CDN_URL } from "../utils/constants";
import { useDispatch } from "react-redux";
import { toast, ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const ItemsList = ({ items }) => {
const dispatch=useDispatch();

const handleAddItems=(items)=>{
  dispatch(addItems(items));
}

  return items.map((items) => (
    <div key={items?.card?.info?.id} className="flex justify-between p-4 my-4 shadow-lg bg-white ">
      <div  className=" text-left">
        <div>
          <span>{items?.card?.info?.name}</span>
          <br></br>
          <span className="font-bold">
            {" "}
            {" "}
            {items?.card?.info?.price / 100 ||
              items?.card?.info?.defaultPrice / 100}
          </span>
        </div>
        {items?.card?.info?.ratings?.aggregatedRating?.rating?(
           <span className="text-green-700"> 
       
           {items?.card?.info?.ratings?.aggregatedRating?.rating}
         </span>):null
        }
       
      </div>
      <div>
      {items?.card?.info?.imageId ? (
  <img
    className="w-24 h-20"
    src={CDN_URL + items.card.info.imageId}
    alt="Item Image"
  />
) : null}
        <div>
        <button className="bg-black text-white rounded-lg p-2 m-1 px-6 cursor-pointer" 
        onClick={()=>{handleAddItems(items); 
          toast.success("Item added to the cart",{
            position:"top-right",
            autoClose:2000,
          })
         
        }
        }>Add+</button>
       
        </div>
      </div>
    </div>
  ));
};
export default ItemsList;
