import { useSelector } from "react-redux";
import ItemsList from "./ItemsList";
import { clearItems } from "../utils/cartSlice";
import { useDispatch } from "react-redux";

const Cart=()=>{

    const cartItems=useSelector((store)=>store.cart.items)

    const dispatch=useDispatch()
  const handleClearCart=()=>{
dispatch(clearItems())
  }

//   const totalAmount=items?.card?.info?.price.reduce((acc,curr)=>{
//     acc=acc+curr 
//     return acc/100
//   },0)
const totalAmount = cartItems
  .map(item => item?.card?.info?.price/100 || item?.card?.info?.defaultPrice/100) // Extract prices safely
  .reduce((acc, curr) => acc + curr, 0)// Sum and convert to proper currency format

  
    return(
        <div className="m-4 p-4 text-center">
            
            <h1 className="text-3xl font-bold">Cart</h1>
            {cartItems.length===0?(<div> <h2 className="m-5 p-5">Cart is empty <br></br>Add items to the cart!!!!</h2>
            <img className="m-auto" src="https://mir-s3-cdn-cf.behance.net/projects/404/54b13147340145.Y3JvcCw0MDUsMzE3LDAsNDI.png"/></div>):
           ( <button className="p-2 m-2 bg-black text-white rounded-2xl" 
            onClick={handleClearCart}> Clear Cart</button>)}
           
           <div className="w-6/12 m-auto">
            <ItemsList items={cartItems}></ItemsList>
           
           </div>
           <div className="bg-green-500  p-4 w-6/12 m-auto">
           <h1 className="font-bold text-white text-2xl">Total Amount: {totalAmount}</h1> 
           </div>
           </div>
    )
}
export default Cart;